import {AxiosResponse} from "axios";
import {authAxios, userToken} from "../index";
import {NavigateFunction} from "react-router-dom";

interface LoginRequestBody {
    id: string;
    password: string;
}

interface LoginResponse {
    seq: number;
    id: string;
    access_token: string;
}

interface LoginHistoryCount {
    count: number;
}

export const LoginRequest = async (body: LoginRequestBody) => {
    return authAxios.post<LoginResponse, AxiosResponse<LoginResponse>, LoginRequestBody>("/account/auth", body);
}

export const Auth = async () => {
    return authAxios.get("/account/auth");
}

export const GetLoginHistory = (start: number, rows: number) => {
    return authAxios.get("/secret/login_history?start=" + start + "&rows=" + rows)
}

export const GetLoginHistoryCount = () => {
    return authAxios.get<LoginHistoryCount, AxiosResponse<LoginHistoryCount>>("/secret/login_history_count")
}

export const handleNeedLogin = (n: NavigateFunction) => {
    if (!userToken()) {
        n("/member/login")
        return false;
    }
    return true;
}
