import React, {useEffect, useState} from 'react';
import * as AuthHandle from "./actions/AuthHandle";
import getAuthStore from "./state/AuthState";
import {IsAdmin, userToken} from "./client";
import './App.css';
import {Auth} from "./client/auth";
import {Container, Nav, Navbar} from "react-bootstrap";
import {allMenus} from "./component/menus";
import Home from "./pages/home";
import "bootstrap/dist/css/bootstrap.min.css";
import {BrowserRouter as Router, NavLink, Route, Routes} from 'react-router-dom';
import NoMatch from "./pages/nomatch";

const App = () => {
    const store = getAuthStore(false);
    const [isAdmin, setIsAdmin] = useState(store.getState().status.currentUser === "admin");
    store.subscribe(() => {
        setIsAdmin(store.getState().status.currentUser === "admin");
    })
    useEffect(() => {
        let id = setInterval(function f() {
            if (userToken()) {
                Auth().catch(() => {
                    store.dispatch(AuthHandle.logout());
                })
            }
            return f;
        }(), 3000);
        return () => clearInterval(id);
    }, []);

    const activeStyle = {
        flexDirection: 'row',
        transition: 'color 0.2s ease-out',
        color: 'white',
        fontWeight: 700,
        margin: "10px",
        textDecoration: "none",
    }

    const nonActiveStyle = {
        color: 'white',
        fontWeight: 300,
        margin: "10px",
        textDecoration: "none",
    }

    return (
      <Router>
          <div style={{overflowX: "hidden", overflowY: "auto"}}>
              <Navbar bg="primary" variant="dark">
                  <Container style={{padding: "10px 10px 10px 20px"}}>
                      <Navbar.Brand href="/">App</Navbar.Brand>
                      <Nav className="me-auto">
                          {
                              allMenus.filter((menu, index) => menu.title &&  (IsAdmin() || isAdmin || (!IsAdmin() && !menu.isAdmin))).map((menu, index) => <NavLink key={index} to={menu.link} style={({isActive}) => isActive ? activeStyle : nonActiveStyle}>{menu.title}</NavLink>)
                          }
                      </Nav>
                  </Container>
              </Navbar>
              <Routes>
                  <Route path="/" element={<Home/>}/>
                  {
                      allMenus.map((menu, index) => <Route key={index} path={menu.link} element={React.createElement(menu.component)}/>)
                  }
                  <Route element={<NoMatch/>}/>
              </Routes>
          </div>
      </Router>
);
}

export default App;
