import { createStore } from 'redux';

let Store: any = null;

interface AuthStateLogin {
    status: string;
}

interface AuthStateStatus {
    isLoggedIn?: boolean;
    currentUser?: string;
    valid?: boolean;
    redirectTo?: string;
}

interface AuthState {
    login?: AuthStateLogin;
    status?: AuthStateStatus;
}

const initState: AuthState =
    {
        login: {
            status: 'FAILURE'
        },
        status: {
            isLoggedIn: false,
            currentUser: '',
            valid: false,
            redirectTo: ''
        }
    };

const authReducer = (state: any, action: any): AuthState => {
    let newState: AuthState = Object.assign({}, initState);
    if (state === undefined) {
        return newState;
    }

    // console.log('ACTION TYPE: ' + action.type);
    switch(action.type) {
        case 'AUTH_LOGIN':
            newState.login = {status: 'WAITING'};
            return newState;
        case 'AUTH_LOGOUT':
            newState.status = {
                valid: false,
                isLoggedIn: false,
            }
            return newState;
        case 'AUTH_LOGIN_SUCCESS':
            newState.login = {status: 'SUCCESS'};
            newState.status = {
                isLoggedIn: true,
                currentUser: action.username,
                redirectTo: ''
            };
            return newState;
        case 'AUTH_LOGIN_FAILURE':
            newState.login = {status: 'FAILURE'};
            return newState;
        case 'REGISTER_REDIRECTION_PATH':
            newState = Object.assign({}, state);
            if (newState.status) {
                newState.status.redirectTo = action.redirectTo;
            }
            return newState;
        case 'AUTH_GET_STATUS':
            newState.status = {
                isLoggedIn: true,
                currentUser: action.userid,
            };
            return newState;
        case 'AUTH_GET_STATUS_SUCCESS':
            newState = Object.assign({}, state);
            if (newState.status) {
                newState.status.valid = true;
                newState.status.currentUser = action.username;
            }
            return newState;
        case 'AUTH_GET_STATUS_FAILURE':
            newState.status = {
                valid: false,
                isLoggedIn: false,
            }
            return newState;
        default:
            return {};
    }
}

function getAuthStore(init: boolean | undefined) {
    if (init || Store === null)
        Store = createStore(authReducer);

    return Store;
}

export default getAuthStore;
