import {authAxios} from "../index";
import {AxiosResponse} from "axios";

export interface AccountInfo {
    id: string;
    name: string;
    phone_no: string;
}

export interface RegisterAccountRequest {
    id: string;
    pwd: string;
    name: string;
    phone_no: string;
}

export const GetAccountInfo = async() => {
    return authAxios.get<AccountInfo, AxiosResponse<AccountInfo>>("/account/info");
}

export const UpdateAccountInfo = async(accountInfo: AccountInfo) => {
    return authAxios.put("/account/info", accountInfo)
}

export const UpdatePassword = async(pwd: string) => {
    return authAxios.put("/account/password", {pwd: pwd})
}

export const RegisterAccount = async(account: RegisterAccountRequest) => {
    return authAxios.post("/account/register", account)
}
