// eslint-disable-next-line @typescript-eslint/no-unused-vars
import React, { Component } from 'react';

import {Avatar, Container, createTheme, CssBaseline, ThemeProvider} from "@mui/material";
import Box from "@mui/material/Box";
import Button from '@mui/material/Button';
import Typography from "@mui/material/Typography";
import Grid from "@mui/material/Grid";
import TextField from "@mui/material/TextField";
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import {useNavigate} from "react-router-dom";
import {RegisterAccount} from "../../../client/account";

const fieldCheck = (data: any) => {
    if (!data.id || data.id.toString().length <= 0) {
        alert('아이디를 입력하세요.');
        return false;
    }
    if (!data.pwd || data.pwd.toString().length <= 0) {
        alert('비밀번호를 입력하세요.');
        return false;
    }
    if (!data.pwd_confirm || data.pwd_confirm.toString() !== data.pwd.toString()) {
        alert('비밀번호가 서로 다릅니다.');
        return false;
    }
    if (!data.name || data.name.toString().length <= 0) {
        alert('이름을 입력하세요.');
        return false;
    }
    if (!data.phone_no || data.phone_no.toString().length <= 0) {
        alert('핸드폰 번호를 입력하세요.');
        return false;
    }

    return true;
}
const Register = () => {
    const theme = createTheme();
    const n = useNavigate();
    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        const body = {
            id: data.get('id')!.toString(),
            pwd: data.get('pwd')!.toString(),
            name: data.get('name')!.toString(),
            phone_no: data.get('phoneNo')!.toString(),
        }
        if (!fieldCheck(body)) return;


        RegisterAccount(body).then(() => {
            alert('회원가입에 성공하였습니다.');
            n('/member/login');
        }).catch((error) => {
            if (error.response && error.response.data.code === 4009)
                alert("이미 존재하는 아이디입니다.")
            else {
                alert("알수 없는 오류로 실패하였습니다.")
                console.log(error.response);
            }
        })
    };
    return (
        <ThemeProvider theme={theme}>
            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <Box
                    sx={{
                        marginTop: 8,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >
                    <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                        <LockOutlinedIcon />
                    </Avatar>
                    <Typography component="h1" variant="h5">
                        Sign up
                    </Typography>
                    <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
                        <Grid container spacing={2}>
                            <Grid item xs={12}>
                                <TextField
                                    required
                                    fullWidth
                                    id="id"
                                    label="아이디"
                                    name="id"
                                    autoComplete="email"
                                    autoFocus
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    required
                                    fullWidth
                                    name="pwd"
                                    label="비밀번호"
                                    type="password"
                                    id="pwd"
                                    autoComplete="new-password"
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    required
                                    fullWidth
                                    name="pwd_confirm"
                                    label="비밀번호 확인"
                                    type="password"
                                    id="pwd_confirm"
                                    autoComplete="new-password"
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    autoComplete="given-name"
                                    name="name"
                                    required
                                    fullWidth
                                    id="name"
                                    label="이름"
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    autoComplete="given-name"
                                    name="phoneNo"
                                    required
                                    fullWidth
                                    id="phoneNo"
                                    label="핸드폰 번호"
                                />
                            </Grid>
                        </Grid>
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            sx={{ mt: 3, mb: 2 }}
                        >
                            Sign Up
                        </Button>
                    </Box>
                </Box>
            </Container>
        </ThemeProvider>
    );
}

export default Register;
