export function login() {
    return {
        type: 'AUTH_LOGIN'
    };
}

export function logout() {
    return {
        type: 'AUTH_LOGOUT'
    };
}

export function loginSuccess(username: string): AuthStatusUsername {
    return {
        type: 'AUTH_LOGIN_SUCCESS',
        username: username
    };
}

export function loginFailure() {
    return {
        type: 'AUTH_LOGIN_FAILURE'
    };
}

interface AuthStatusUserId {
    type: string;
    userid: string;
}

export function getStatus(userid: string): AuthStatusUserId {
    return {
        type: 'AUTH_GET_STATUS',
        userid: userid
    };
}

interface AuthStatusUsername {
    type: string;
    username: string;
}

export function getStatusSuccess(username: string): AuthStatusUsername {
    return {
        type: 'AUTH_GET_STATUS_SUCCESS',
        username: username
    };
}

export function getStatusFailure() {
    return {
        type: 'AUTH_GET_STATUS_FAILURE'
    };
}
