// eslint-disable-next-line @typescript-eslint/no-unused-vars
import React, {Component, useEffect, useState} from 'react';
import getAuthStore from '../../../state/AuthState';
import {Navigate, useNavigate} from 'react-router-dom'

import * as AuthHandle from '../../../actions/AuthHandle';
import {Avatar, Container, CssBaseline} from "@mui/material";
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Box from "@mui/material/Box";
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import {LoginRequest} from "../../../client/auth";
import {isAdminKey, userTokenKey} from "../../../client";


const Login = () => {
    const store = getAuthStore(false);
    const n = useNavigate();
    const [isLoggedIn, setIsLoggedIn] = useState(store.getState().status.isLoggedIn);
    store.subscribe(() => {
        setIsLoggedIn(store.getState().status.isLoggedIn);
    });

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        const id: FormDataEntryValue | null = data.get('id');
        const pwd = data.get('pwd');
        if (!id || id.toString().length <= 0) {
            alert('아이디를 입력하세요.');
            return;
        }
        if (!pwd || pwd.toString().length <= 0) {
            alert('비밀번호를 입력하세요.');
            return;
        }
        store.dispatch(AuthHandle.login());
        LoginRequest({id: id.toString(), password: pwd.toString()}).then(
            (resp) => {
                store.dispatch(AuthHandle.loginSuccess(resp.data.id));
                localStorage.setItem(userTokenKey, resp.data.access_token);
                localStorage.setItem(isAdminKey, id.toString() === "admin" ? "true" : "false")

                n("/");
            }
        ).catch((error) => {
            if (error.response.status === 401 && error.response.data)
                alert(error.response.data.err);
            store.dispatch(AuthHandle.loginFailure());
        });
    }

    useEffect(() => {
    }, []);

    if (isLoggedIn) {
        return <Navigate replace to='/'/>;
    }

    return (
        <Container component="main" maxWidth="xs">
            <CssBaseline />
            <Box
                sx={{
                    marginTop: 8,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                }}
                >
                <Avatar sx={{m: 1, bgcolor: 'secondary.main' }}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                    Login
                </Typography>
                <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
                    <TextField
                        margin="normal"
                        required
                        fullWidth
                        id="id"
                        label="ID"
                        name="id"
                        autoFocus
                    />
                    <TextField
                        margin="normal"
                        required
                        fullWidth
                        name="pwd"
                        label="Password"
                        type="password"
                        id="pwd"
                        autoComplete="current-password"
                    />
                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        sx={{ mt: 3, mb: 2 }}
                    >
                        Sign In
                    </Button>
                </Box>
            </Box>
        </Container>
    )
}

export default Login;
