import axios, {AxiosError, AxiosResponse} from "axios";
import * as AuthHandle from "../actions/AuthHandle";
import getAuthStore from "../state/AuthState";

const host = ""
const port = 0

const prefix = "v1"
const baseUrl = `http://${host}:${port}/${prefix}`

export const userTokenKey = "userToken";
export const isAdminKey = "isAdmin"

export const userToken = (): string | null => {
    return localStorage.getItem(userTokenKey);
}

export const IsAdmin = () => {
    return localStorage.getItem(isAdminKey) === "true"
}

export function destroyToken() {
    localStorage.removeItem(userTokenKey);
    localStorage.removeItem(isAdminKey)
}

export const authAxios = axios.create({
    baseURL: baseUrl
})

authAxios.interceptors.request.use(
    config => {
        const token = userToken();
        if (config.headers && !config.headers.Authorization && token && token.length > 0) {
            config.headers.Authorization = token;
        }
        if (config.headers && !config.headers["Content-Type"])
            config.headers["Content-Type"] = "application/json";

        return config;
    },
    error => {
        console.log(error)
        return Promise.reject(error);
    },
);

interface ResponseError {
    code: number;
    err: string;
}

authAxios.interceptors.response.use(
    (response: AxiosResponse) => {
        console.log(response)
        return response;
    },
    (e: AxiosError<ResponseError>) => {
        if (e.response && e.response.data && (e.response.data.code === 4003 || e.response.data.code === 4002)) {
            destroyToken();
            const store = getAuthStore(false);
            store.dispatch(AuthHandle.logout());
            return
        } else if (e.response && e.response.data && e.response.data.code === 4001) {
            destroyToken();
            const store = getAuthStore(false);
            store.dispatch(AuthHandle.logout());
        }
        return Promise.reject(e);
    },
);
